.App {
  text-align: center;
  display: flex;
  flex-direction: column;
}
div {
  box-sizing: border-box;
}
.btn {
  text-align: center;
  background-color: black;
  color:white;
  border-radius: 10px;
  padding:10px;
  border: none;
  width:100px;
  margin-left:20px;
}
.navBar {
  width: 100%;
  background-color: black;
  color:white;
  text-align: left;
  margin:0px;
  padding:5px;
  padding-left:20px;
}
.list {
  text-align: left;
  border-bottom: 1px solid rgb(202, 202, 202);
  padding-left:30px;
}
.Modal {
  background-color: #eee;
  text-align: left;
  padding-left:30px;
  padding-bottom: 30px;
}
.Modal h4 {
  font-size: 20px;
}
.input-group {
  display:flex;
  float: left;
  padding:20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
